import React, { useState } from 'react';
import {
  Carousel,
  CarouselItem,
  CarouselControl,
  CarouselIndicators,
  CarouselCaption
} from 'reactstrap';
import { TabContent, TabPane, Nav, NavItem, NavLink, Card, Button, CardTitle, CardText, Row, Col } from 'reactstrap';
import classnames from 'classnames';

const items = [
  {
    id: 1,
    src: 'https://i.pinimg.com/originals/0f/1a/62/0f1a62b16b9b11a36332839deaaf29d6.jpg',
    altText: '',
    caption: ''
  },
  {
    id: 2,
    src: 'https://wallpaperaccess.com/full/1426870.png',
    altText: '',
    caption: ''
  },
  {
    id: 3,
    src: 'https://trusaic.com/wp-content/uploads/2018/07/AdobeStock_210275088.jpeg',
    altText: '',
    caption: ''
  }
];

const HomePage = (props) => {
  const [activeIndex, setActiveIndex] = useState(0);
  const [animating, setAnimating] = useState(false);
  const [activeTab, setActiveTab] = useState('1');

  const toggle = tab => {
    if(activeTab !== tab) setActiveTab(tab);
  }

  const next = () => {
    if (animating) return;
    const nextIndex = activeIndex === items.length - 1 ? 0 : activeIndex + 1;
    setActiveIndex(nextIndex);
  }

  const previous = () => {
    if (animating) return;
    const nextIndex = activeIndex === 0 ? items.length - 1 : activeIndex - 1;
    setActiveIndex(nextIndex);
  }

  const goToIndex = (newIndex) => {
    if (animating) return;
    setActiveIndex(newIndex);
  }

  const slides = items.map((item) => {
    return (
      <CarouselItem
        className="custom-tag"
        tag="div"
        key={item.src}
        onExiting={() => setAnimating(true)}
        onExited={() => setAnimating(false)}
      >
        <img src={item.src} alt={item.altText} />
        <CarouselCaption className="text-white" captionText={item.caption} captionHeader={item.caption} />
      </CarouselItem>
    );
  });

  return (
    <div>
      <style>
        {
          `.custom-tag {
              max-width: 100%;
              height: 375px;
            }`
        }
      </style>
      <Carousel
        activeIndex={activeIndex}
        next={next}
        previous={previous}
      >
        <CarouselIndicators items={items} activeIndex={activeIndex} onClickHandler={goToIndex} />
        {slides}
        <CarouselControl direction="prev" directionText="Previous" onClickHandler={previous} />
        <CarouselControl direction="next" directionText="Next" onClickHandler={next} />
      </Carousel>

        <br/><br/>

      <div className='container'>
      <Nav tabs>
        <NavItem>
          <NavLink
            className={classnames({ active: activeTab === '1' })}
            onClick={() => { toggle('1'); }} style={{fontSize:"15px"}}
          >
            About Us
          </NavLink>
        </NavItem>
        <NavItem>
          <NavLink
            className={classnames({ active: activeTab === '2' })}
            onClick={() => { toggle('2'); }} style={{fontSize:"15px"}}
          >
            Objectives
          </NavLink>
        </NavItem>
        <NavItem>
          <NavLink
            className={classnames({ active: activeTab === '3' })}
            onClick={() => { toggle('3'); }} style={{fontSize:"15px"}}
          >
            Contact Us
          </NavLink>
        </NavItem>
      </Nav>
      <TabContent activeTab={activeTab}>
        <TabPane tabId="1">
          <Row>
            <Col sm="12">
                <br></br>
                <Card body>
                <CardTitle style={{fontWeight:"bold", fontSize:"18px"}}>Virtual Labs</CardTitle>
                <CardText style={{fontSize:"15px"}}>We at Sardar Patel Institute of Technology have started Virtual Lab which focuses on providing students with laboratory course work material.
                  <br></br>Virtual Labs will provide to the students the result of an experiment by one of the following methods (or possibly a combination). Modeling the physical phenomenon by a set of equations and carrying out simulations to yield the result of the particular experiment. This can, at-the-best, provide an approximate version of the real-world experiment. Providing measured data for virtual lab experiments corresponding to the data previously obtained by measurements on an actual system. Remotely triggering an experiment in an actual lab and providing the student the result of the experiment through the computer interface. This would entail carrying out the actual lab experiment remotely.</CardText>
                {/* <Button>Go somewhere</Button> */}
              </Card>
            </Col>
          </Row>
        </TabPane>
        <TabPane tabId="2">
          <Row>
            <Col sm="4" className="col-xs-12 col-sm-12 col-md-4">
            <br></br>
              <Card body >
                <CardTitle style={{fontWeight:"bold", fontSize:"18px"}}>Boost enthusiasm</CardTitle>
                <CardText style={{fontSize:"15px"}}>Through this lab we aim to foster learning. To enthuse students to conduct experiments by the arousing their curiosity. This would help them in learning basic and advanced concepts through remote experimentation. And help the student try and innovate things.</CardText>
                {/* <Button>Go somewhere</Button> */}
              </Card>
            </Col>
            <Col sm="4" className="col-xs-12 col-sm-12 col-md-4">
                <br></br>
              <Card body>
                <CardTitle style={{fontWeight:"bold", fontSize:"18px"}}>Remote-access to Labs for everyone</CardTitle>
                <CardText style={{fontSize:"15px"}}>To provide remote-access to Labs in various disciplines of Science and Engineering. These Virtual Labs would cater to students at the undergraduate level, post graduate level as well as to research scholars (everyone).</CardText>
                {/* <Button>Go somewhere</Button> */}
              </Card>
            </Col>
              <Col sm="4" className="col-xs-12 col-sm-12 col-md-4">
                <br></br>
              <Card body>
                <CardTitle style={{fontWeight:"bold", fontSize:"18px"}}>Complete Learning Management System</CardTitle>
                <CardText style={{fontSize:"15px"}}>To provide a complete Learning Management System around the Virtual Labs where the students can avail the various tools for learning which will help in animated demonstrations and self evaluation.</CardText>
                {/* <Button>Go somewhere</Button> */}
              </Card>
              </Col>
              
          </Row>
        </TabPane>
        <TabPane tabId="3">
          <Row>
            <Col sm="12">
                <br></br>
                <Card body>
                <CardTitle style={{fontWeight:"bold", fontSize:"18px"}}>Connect with us:</CardTitle>
                <CardText style={{fontSize:"15px"}}>
                <p>
                <b>Sardar Patel Institute Of Technology</b></p>
                <p><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M10,1.375c-3.17,0-5.75,2.548-5.75,5.682c0,6.685,5.259,11.276,5.483,11.469c0.152,0.132,0.382,0.132,0.534,0c0.224-0.193,5.481-4.784,5.483-11.469C15.75,3.923,13.171,1.375,10,1.375 M10,17.653c-1.064-1.024-4.929-5.127-4.929-10.596c0-2.68,2.212-4.861,4.929-4.861s4.929,2.181,4.929,4.861C14.927,12.518,11.063,16.627,10,17.653 M10,3.839c-1.815,0-3.286,1.47-3.286,3.286s1.47,3.286,3.286,3.286s3.286-1.47,3.286-3.286S11.815,3.839,10,3.839 M10,9.589c-1.359,0-2.464-1.105-2.464-2.464S8.641,4.661,10,4.661s2.464,1.105,2.464,2.464S11.359,9.589,10,9.589"></path></svg>&nbsp;&nbsp; Bhavans Campus, Munshi Nagar, Andheri (West), Mumbai 400 058</p>
                <p><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M20 4H4c-1.1 0-1.99.9-1.99 2L2 18c0 1.1.9 2 2 2h16c1.1 0 2-.9 2-2V6c0-1.1-.9-2-2-2zm0 4l-8 5-8-5V6l8 5 8-5v2z"/></svg>
                &nbsp;&nbsp; drkalbande@spit.ac.in</p>
                <p><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M6.62 10.79c1.44 2.83 3.76 5.14 6.59 6.59l2.2-2.2c.27-.27.67-.36 1.02-.24 1.12.37 2.33.57 3.57.57.55 0 1 .45 1 1V20c0 .55-.45 1-1 1-9.39 0-17-7.61-17-17 0-.55.45-1 1-1h3.5c.55 0 1 .45 1 1 0 1.25.2 2.45.57 3.57.11.35.03.74-.25 1.02l-2.2 2.2z"/></svg>								
                &nbsp;&nbsp; (+91) 98203 83928</p>
                </CardText>
                {/* <Button>Go somewhere</Button> */}
              </Card>
            </Col>
          </Row>
        </TabPane>
      </TabContent>
      </div>
      
    </div>
  );
}

export default HomePage;
