import React, { Component } from 'react'
import { Link } from 'react-router-dom';
import {Button} from 'reactstrap'

export default class ResNetSimulationComponent extends Component {
    render() {
        return (
            <>
                <Link to="/aboutcourse-resnet">
                    <Button size="sm"
                        style={{ width: 150, margin: 'auto', fontWeight: "bold", border: '2px solid' }}>
                        Back To About Course
                    </Button>
                </Link>
            <iframe src="https://vlabs_imageclassifier.surge.sh/"
                frameborder="0"
                marginheight="0"
                marginwidth="0"
                width="100%"
                height="100%"
                scrolling="auto">
            </iframe>
            </>
        )
    }
}
