import React, { Component } from 'react'
import {Button} from 'reactstrap'
import {Link} from 'react-router-dom'; 


export default class BackPropagationSimulation extends Component {
  render() {

    return (
      <>
        <Link to="/aboutcourse-backpropagation">
          <Button size="sm"
            style={{ width: 150, margin: 'auto', fontWeight: "bold", border: '2px solid' }}>
            Back To About Course
          </Button>
        </Link>
        <iframe src="https://arnabaghorai.github.io/nn-playground"
          frameborder="0"
          marginheight="0"
          marginwidth="0"
          width="100%"
          height="100%"
          scrolling="auto">
        </iframe>
      </>
    )
  }
}