import React, { Component } from 'react'
import { Link } from 'react-router-dom';
import {Button} from 'reactstrap';

export default class SVMSimulation extends Component {
  render() {
    return (
      <>
        <Link to="/aboutcourse-svm">
          <Button size="sm"
            style={{ width: 150, margin: 'auto', fontWeight: "bold", border: '2px solid' }}>
            Back To About Course
          </Button>
        </Link>
      <iframe src="https://taniarajabally.github.io/Vlabs/"
        frameborder="0"
        marginheight="0"
        marginwidth="0"
        width="100%"
        height="100%"
        scrolling="auto">
      </iframe>
      </>
    )
  }
}